// import cityList from '../../components/city.data.js'
import request from '../../util/request.js'
export default {
	data() {
		return {
			address:{
				  "address": '',
				  "city": '',
				  "customerId": '',
				  "district": '',
				  "isDefault": 0,
				  "province": '',
				  "shippingPhone":'',
				  "shippingUser": ''
			}
		}
	},
	components: {
	},
	methods: {
		 switch1Change: function (e) {
		            console.log('switch1 发生 change 事件，携带值为', e.target.value)
					let i;
					if(e.target.value==true||e.target.value=='true'){
						i=1;
						this.address.isDefault=i;
					}else{
						i=0;
						this.address.isDefault=i;
					}
		},
		submitAddress:function(){
			let id;
			try {
			    let u = uni.getStorageSync('user');
			    if (u) {
					id = u.id;
			    }
			} catch (e) {
			    // error
				console.log("啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊")
				console.log(e)
			}
			this.address.customerId=id;
			console.log("+-+-+-+-+-+-+-+-+-+-+--++-+--+-+--")
			console.log(this.address)
			let ins={
				url:'/customer/addrs',
				method:'post'
			}
			let data=this.address;
			request.TokenRequest(ins,data).then(res=>{
				console.log(res.data)
				uni.showToast({
					title: res.message,
					duration: 2000
				});
				uni.navigateBack({
					delta:1
				})
			}).catch(error=>{
				console.log(error)
			});
		}
					
	}
}
